.nav-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 100;
  display: flex;
  visibility: hidden;
  pointer-events: none; }
  .nav-modal.menu-open {
    visibility: visible;
    pointer-events: auto; }
    .nav-modal.menu-open .nav-menu {
      transition: transform 400ms cubic-bezier(0.46, 0.09, 0.48, 1.04);
      transform: scale(1, 1); }
    @media only screen and (min-width: 48em) {
