        opacity: 1; } }

.nav-menu {
  position: relative;
  font-weight: bold;
  text-transform: lowercase;
  text-align: right;
  width: 50%;
  transform: scale(0, 1);
  height: 100%;
  background-color: #1B1B1B;
  overflow: auto;
  color: white;
  transition: transform 400ms cubic-bezier(0.46, 0.09, 0.48, 1.04);
  transform-origin: right center; }
  .nav-menu ul {
    display: block;
    width: 100%;
    height: 100%;
    font-weight: 400;
    padding-left: 0;
    margin-top: 0; }
    @media only screen and (min-width: 48em) {
