        padding: 12em 6vw 0 0; } }
    .nav-menu ul li {
      padding-bottom: 1.38889em;
      font-size: 1.8em;
      opacity: 0;
      transform: translateX(25%);
      transition: transform 400ms cubic-bezier(0.46, 0.09, 0.48, 1.04), opacity 400ms cubic-bezier(0.46, 0.09, 0.48, 1.04) 150ms; }
      @media only screen and (min-width: 48em) {
