          font-size: 4em;
          padding-bottom: 0.75em; } }
      .nav-menu ul li:last-child {
        padding-bottom: 0; }
      .nav-menu ul li.appear {
        opacity: 1;
        transform: translateX(0); }

.nav-background-image {
