    height: 3px;
    margin-bottom: 6px;
    transition: 300ms ease;
    position: relative; }
    @media only screen and (min-width: 48em) {
      .hamburger-icon span {
        margin-bottom: 8px; } }
    .hamburger-icon span:first-child, .hamburger-icon span:last-child {
      transform: rotate(0);
      top: 0; }
    .hamburger-icon span:last-child {
      margin-bottom: 0; }
  .hamburger-icon.menu-open {
    position: relative;
    z-index: 100; }
    .hamburger-icon.menu-open span {
      transition: 300ms ease; }
      .hamburger-icon.menu-open span:first-child {
        transform: rotate(45deg);
        top: 8px; }
      .hamburger-icon.menu-open span:nth-child(2) {
        opacity: 0; }
      .hamburger-icon.menu-open span:last-child {
        transform: rotate(-45deg);
        top: -8px; }

